import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SubmittalPackageComponent } from './index';
import { CurrentUserResolver } from '../shared/services/common/user-resolver.service';
import { QuoteEditResolver } from '../quotes/services/quote-resolver.service';

const submittalPackageRoutes: Routes = [
    {
        path: 'submittalPackage/:projectid/:quoteid',
        component: SubmittalPackageComponent,
        resolve: {
            quoteModel: QuoteEditResolver,
            currentUser: CurrentUserResolver
        }
    }
];

@NgModule({
    imports: [RouterModule.forChild(submittalPackageRoutes)],
    exports: [RouterModule],
})
export class SubmittalPackageRoutingModule { }


