import { NgModule } from '@angular/core';

import { SharedModule } from '../shared/shared.module';
import { QuotesModule } from '../quotes/quotes.module';
import { DragulaModule } from 'ng2-dragula'; 
import { UploadModule } from '@progress/kendo-angular-upload';  

import { SubmittalPackageRoutingModule } from './submittal-routing.module';

import {
    AttachedFilesComponent,
    ModalComponent,
    QuotePackageGridComponent,
    SubmittalPackageComponent,   
    SubmittalPackageService
} from './index';

export const SUBMITTALPACKAGE_COMPONENTS = [
    AttachedFilesComponent,
    QuotePackageGridComponent,
    SubmittalPackageComponent,
    ModalComponent
]

@NgModule({
    imports: [
        SharedModule.forRoot(),
        DragulaModule,
        UploadModule,
        SubmittalPackageRoutingModule,
        QuotesModule
    ],
    exports: SUBMITTALPACKAGE_COMPONENTS,
    declarations: SUBMITTALPACKAGE_COMPONENTS,
    providers: [
        SubmittalPackageService
    ]
})
export class SubmittalPackageModule { }



